.App {
  text-align: center;
}
h2,
p,
ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.2;
  background-color: #ddf8fd;
}

#root {
  padding-top: 15px;
  display: flex;
  flex-wrap: wrap;
}

.profile {
  width: 300px;
  height: 100%;
  margin: 0 auto 30px;
  padding-top: 30px;
  color: #000;
  overflow: hidden;
  background-color: #fff;
  border: 2px solid #e0e0e0;
  border-radius: 5px;
}

.description {
  text-align: center;
  margin-bottom: 30px;
}

.avatar {
  margin-bottom: 30px;
  border-radius: 50%;
}

.name {
  font-weight: 700;
  font-size: 25px;
  margin-bottom: 15px;
}

.tag {
  margin-bottom: 5px;
}

.stats {
  display: flex;
  border-top: 1px solid #e0e0e0;
}

.stats > li {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: calc(100% / 3);
  height: 80px;
  text-align: center;
  background-color: #fbfddd;
}

.stats > li:not(:last-child) {
  border-right: 1px solid #e0e0e0;
}

.label {
  margin-bottom: 5px;
}

.quantity {
  font-weight: 700;
  font-size: 16px;
}

.tag,
.location,
.label {
  color: #a9a9a9;
}

.statistics {
  width: 300px;
  height: 100%;
  margin: 0 auto 30px;
  padding-top: 30px;
  overflow: hidden;
  border-radius: 5px;
  background-color: #fff;
}

.title {
  color: #575757;
  text-align: center;
  margin-bottom: 30px;
}

.stat-list {
  display: flex;
}

.item {
  width: 25%;
  height: 60px;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  color: #fff;
}

.item > .label {
  color: #fff;
}

.friend-list {
  margin: 0 auto 30px;
  width: 300px;
}

.friend-list > .item {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: auto;
  height: auto;
  padding: 15px;
  border-radius: 5px;
  color: #000;
  background-color: rgb(241, 241, 241);
  box-shadow: 10px 4px 10px 4px rgba(34, 60, 80, 0.2);
}

.friend-list > .item:not(:last-child) {
  margin-bottom: 15px;
}

.friend-list > .item > .avatar {
  width: 80px;
  margin-right: 30px;
  margin-bottom: 0;
  border-radius: 5px;
}

.friend-list > .item > .name {
  margin-bottom: 0;
}

.status {
  width: 25px;
  height: 25px;
  margin-right: 30px;
  border-radius: 50%;
  background-color: #ff0000bf;
}

.status.online {
  background-color: #0dee0d;
}

.friend-list > .item.online {
  background-color: #fff;
  box-shadow: 4px 4px 4px 4px rgba(34, 60, 80, 0.2);
}

.transaction-history {
  margin: 0 auto 30px;
  width: 95%;
  min-width: 320px;
  max-width: 500px;
  border-collapse: collapse;
  border: 1px solid transparent;
  border-radius: 5px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

.transaction-history > thead {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  background-color: #0087b4;
}

.transaction-history th,
.transaction-history td {
  padding: 15px;
  border: none;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

.transaction-history td {
  text-align: center;
}

.transaction-history tr:nth-child(even) {
  background-color: #eceaea;
}

.transaction-history thead th:first-child {
  border-radius: 5px 0 0 0;
}

.transaction-history thead th:last-child {
  border-radius: 0 5px 0 0;
}

.transaction-history tbody tr:last-child td:first-child {
  border-radius: 0 0 0 5px;
}

.transaction-history tbody tr:last-child td:last-child {
  border-radius: 0 0 5px 0;
}
